<script>
import { ref, computed, onMounted } from 'vue'
import { useRoute, useRouter } from "vue-router"
import { Toast } from '../../components/global/swal'
import { errorHandle } from '../../utils/handle'
import { apiRegisterEmployer, apiRegisterCaretaker, apiOtpRegister, apiPostRegisterV2 } from '@/api/api'
import { useCoolDownStore } from "@/store/otpCoolDown"
import { storeToRefs } from "pinia";
import { onBeforeRouteLeave } from "vue-router"
import { focusInput } from '@/utils/fixFooter'


export default {
  setup() {
    //======簡訊冷卻start
    const smsCoolDown = useCoolDownStore();
    const { showText, setCountDown, smsSendDate, handleRouterLeave, init } = smsCoolDown;
    const { isSmsCoolDownOk } = storeToRefs(smsCoolDown);//動態刷新參數
    const showSendOtpBtn = computed(() => (showText() ? false : true)); //畫面顯示用的flag
    const line_name = ref("")
    const handleLineQuery = () => {
      const route = useRoute()
      const { line_access_token } = route.query
      if (line_access_token) {
        line_name.value = route.query.line_name
        // 代表有拿到
        inputData.value.line_access_token = line_access_token

      }
    }

    const handleSendOtp = async () => {
      // ======== 判斷發送簡訊 是否冷ing 
      if (isSmsCoolDownOk.value === false) {
        alert("簡訊功能冷卻中");
        return;
      } else {
        // ======== 不是冷卻中 開始做發送簡訊
        const msg = await apiOtpRegister({ "phoneNumber": inputData.value.phoneNumber })
        // msg = { status: true }
        if (msg.status == true) {
          Toast("驗證碼已發送")
          isSendVerify.value = true
          smsSendDate();// 記錄發送簡訊的時間
          setCountDown();// 記錄目前的時間
        } else {
          const errMessage = errorHandle(msg)
          Toast(errMessage)
        }
      }
    };

    onMounted(() => {
      handleLineQuery()
      focusInput()
      init();// 計時器初始化
      setCountDown();//  記錄目前的時間
    });

    onBeforeRouteLeave((to, from, next) => {
      handleRouterLeave(); //歸零+清除計時器
      next();
    });
    //======簡訊冷卻end
    const router = useRouter()
    const inputData = ref({
      phoneNumber: "",
      password: "",
      verifyCode: "",
      roleType: "caretaker",
      line_access_token: "",      
    })
    const password2 = ref("")
    // let verifyCode = ref()

    const isSendVerify = ref(false)
    const form = ref(null)
    const form2 = ref(null)
    let msg = ""

    const sendVerify = async ($event) => {
      $event.preventDefault()
      if (form.value.reportValidity()) {
        handleSendOtp()
      }
    }
    const sendVerify2 = async ($event) => {
      $event.preventDefault()
      if (form.value.reportValidity()) {
        handleSendOtp()
      }
    }
    const register = async ($event) => {
      $event.preventDefault()
      if (form2.value.reportValidity()) {
        if (inputData.value.password != password2.value) {
          Toast('密碼不相符，請確認')
        } else {
          //新增會員資料 S-1-1-2
          // console.log(inputData.value)
          if (process.env.VUE_APP_MODE == "purple") {
            //   msg = await apiRegisterEmployer(inputData.value)
            // } else {
            //   msg = await apiRegisterCaretaker(inputData.value)
            inputData.value.roleType = "employer"
          }
          msg = await apiPostRegisterV2(inputData.value)
          
          if (msg.status == true) {
            Toast("加入會員成功")
            router.push({ path: "/" })
          } else {
            const errMessage = errorHandle(msg)
            Toast(errMessage)
          }
        }
      }
    }
    let isShowPassword = ref(0)
    let isShowPassword2 = ref(0)
    const showPassword = () =>{
      isShowPassword.value = isShowPassword.value == 0 ? 1 : 0 
    }
    const showPassword2 = () =>{
      isShowPassword2.value = isShowPassword2.value == 0 ? 1 : 0 
    }

    return { showText, showSendOtpBtn, inputData, form, form2, register, isSendVerify, sendVerify, sendVerify2, password2,isShowPassword,showPassword,isShowPassword2,showPassword2,line_name };
  }
}
</script>

<template>
  <div class="fullScreen">
    <div class="signup">
      <div class="title">會員註冊</div>
      <div class="signup-main">
        <div v-if="line_name">hi! {{ line_name }}， 您目前正以line方式註冊</div>
        <form ref="form">
          <div class="mb-3">
            <input type="text" class="form-control" placeholder="請輸入手機號碼" v-model="inputData.phoneNumber"
              pattern="^09\d{2}?\d{3}?\d{3}$" title="請輸入手機號碼" required>
          </div>
        </form>
        <div class="mb-3" v-if="isSendVerify">
          <input type="text" class="form-control" placeholder="請輸入驗證碼" v-model="inputData.verifyCode">
        </div>
        <form ref="form2">
          <div class="mb-3 passwordArea" v-if="isSendVerify">
            <div v-if="isShowPassword == 0">
              <input type="password" class="form-control" placeholder="請輸入密碼" v-model="inputData.password"
                pattern="^(?=.*[A-Za-z])(?=.*[0-9]).{6,}$" title="最少6個字元，需有英文及數字" required />
              <a class="nav-link" @click="showPassword"><i class="far fa-eye"></i></a>
            </div>
            <div v-if="isShowPassword == 1">
              <input type="text" class="form-control" placeholder="請輸入密碼" v-model="inputData.password"
                pattern="^(?=.*[A-Za-z])(?=.*[0-9]).{6,}$" title="最少6個字元，需有英文及數字" required />
              <a class="nav-link" @click="showPassword"><i class="far fa-eye-slash"></i></a>
            </div>
            <!-- <input type="password" class="form-control" placeholder="請輸入密碼" pattern="^(?=.*[A-Za-z])(?=.*[0-9]).{6,}$"
              title="最少6個字元，需有英文及數字" v-model="inputData.password" required> -->
          </div>
        </form>
        <div class="mb-3 passwordArea" v-if="isSendVerify">
          <div v-if="isShowPassword2 == 0">
              <input type="password" class="form-control" v-model="password2"
              placeholder="請再次確認密碼" required />
              <a class="nav-link" @click="showPassword2"><i class="far fa-eye"></i></a>
            </div>
            <div v-if="isShowPassword2 == 1">
              <input type="text" class="form-control" v-model="password2"
              placeholder="請再次確認密碼" required />
              <a class="nav-link" @click="showPassword2"><i class="far fa-eye-slash"></i></a>
            </div>
          <!-- <input type="password" class="form-control" placeholder="請再次確認密碼" v-model="password2" required> -->
        </div>

        <div class="mt-4b btn-container">
          <div class="row">
            <div class="col" v-if="!isSendVerify">
              <button class="btn btn-primary" type="button" @click.prevent="sendVerify($event)">發送驗證碼</button>
            </div>
            <div class="col" v-if="isSendVerify">
              <button class="btn btn-primary" type="button" style="background-color:#BFBFBF;border: 0px;"
                v-if="!showSendOtpBtn">{{ `請稍後${showText()}秒` }}</button>
              <button class="btn btn-primary" type="button" @click="sendVerify2($event)"
                v-if="showSendOtpBtn">重新發送</button>
            </div>
            <div class="col" v-if="isSendVerify">
              <button class="btn btn-primary" type="button" @click="register($event)">加入會員</button>
            </div>
          </div>
        </div>
        <div class="login-main2">
          <div @click="$router.push({ path: '/home' })">訪客登入</div>
          <div>.</div>
          <div @click="$router.push({ path: '/' })">回登入頁</div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.fullScreen {
  overflow-y: hidden;
  width: 100vw;
  height: 100vh;
  background-image: url(#{$img}#{nth($login-bg-image, $style)
});
background-repeat: no-repeat;
background-position: bottom;
background-size: 100vw auto;
}

.signup {
  margin-top: 3.75rem;
  margin-left: 2rem;
  margin-right: 2rem;

  .title {
    font-size: 1.5rem;
    font-weight: 400;
    margin-bottom: 1rem;
  }

  .signup-main {
    .form-control {
      margin: 1.5rem 0;
    }
  }

  .btn {
    width: 7.25rem;
  }
}

.login-main2 {
  margin: 1rem auto;
  display: flex;
  justify-content: center;
  color: #909090;

  div {
    margin: 0 0.2rem;
  }
}

.passwordArea {
  position: relative;

  a {
    position: absolute;
    top: 0rem;
    right: 0rem;
    color: #909090;
  }
}
</style>